// src/components/FoldingIcon.tsx
import React from 'react';
import { Element } from '../types';

interface FoldingIconProps {
    element: Element;
}

const FoldingIcon: React.FC<FoldingIconProps> = ({ element }) => {
    return (
        <g>
            <circle
                cx={element.x + element.width + 5} // Rect
                cy={element.y + element.height / 2} // Rect
                r={4} // 
                fill="none"
                stroke="black"
                strokeWidth="1"
            />
            <text
                x={element.x + element.width + 5} // Rect
                y={element.y + element.height / 2} // Rect
                textAnchor="middle" // 
                dominantBaseline="middle" // 
                fontSize="12" // 
            >
                +
            </text>
        </g>
    );
};

export default FoldingIcon;